
.page-search{position:absolute;top:0;left:0;bottom:50px;width:100%;overflow:auto;-webkit-overflow-scrolling:touch
}
.job-search{display:-ms-flexbox;display:flex;-ms-flex-align:center;align-items:center;-ms-flex-pack:justify;justify-content:space-between;padding:7px 15px;height:44px;line-height:1;background:#f4f4f4;box-sizing:border-box;font-size:12px;border-bottom:1px solid #ddd
}
.job-search__label,.job-search__form,.job-search__cancel{height:30px;line-height:30px;color:#999
}
.job-search__label{min-width:60px;max-width:80px;margin-left:-15px;text-align:center;font-size:0;white-space:nowrap;overflow:hidden;text-overflow:ellipsis
}
.job-search__label .icon{font-size:15px;vertical-align:middle
}
.job-search__label .city{padding:0 5px;font-size:12px;vertical-align:middle
}
.job-search__form{position:relative;-ms-flex:1;flex:1;padding-right:30px;border:1px solid #ddd;border-radius:30px;background:#FFF
}
.job-search__form .input{display:block;height:100%;width:100%;padding:0 10px;border:none;outline:none;background:none;font:inherit;color:#999
}
.job-search__form .input::-webkit-input-placeholder{font:inherit;font-size:11px
}
.job-search__form .icon{position:absolute;top:0;right:0;width:30px;height:30px;line-height:30px;text-align:center;font-size:15px
}
.job-search__cancel{padding:0 20px;margin-right:-15px
}
.job-tags{background:#FFF;box-sizing:border-box
}
.job-tags__name{padding:0 15px;line-height:26px;font-size:12px;font-weight:600;color:#666;background:#f4f4f4
}
.job-tags__name .icon{float:right;margin-right:-15px;padding:0 15px;height:26px;text-align:center;font-size:16px
}
.job-tags__list{position:relative;display:-ms-flexbox;display:flex;-ms-flex-wrap:wrap;flex-wrap:wrap;padding:10px 15px
}
.job-tags__list .tag{height:24px;line-height:24px;margin:2%;padding:0 4%;text-align:center;font-size:12px;border-radius:24px;border:1px solid #ddd;color:#00b38a;font-weight:200;box-sizing:border-box;white-space:nowrap
}
.job-tags__list .tag:active{background:#f6f6f6
}
.job-tags__list .change{position:absolute;right:15px;top:50%;transform:translate3d(0, -50%, 0);font-size:14px;color:#00b38a
}
.job-tags__list.city .tag{width:28%;font-size:13px;color:#999
}
.job-city{position:absolute;left:0;top:0;right:0;bottom:0;z-index:501;background:#FFF
}
.job-city-hd{position:relative;height:44px;line-height:44px;text-align:center;background:#f4f4f4;font-size:16px;font-weight:200;border-bottom:1px solid #ddd;box-sizing:border-box
}
.job-city-hd .icon{position:absolute;right:0;height:44px;padding:0 15px;font-size:18px
}
.job-city-bd{position:absolute;top:44px;left:0;right:0;bottom:0;overflow:auto;-webkit-overflow-scrolling:touch
}



