
.home-search-wrapper{position:absolute;left:0;top:0;width:100%;margin-top:40px;z-index:99
}
.invite-hot img{max-width:100%;vertical-align:top
}
.job-header{position:absolute;left:0;top:0;padding:0 15px;width:100%;height:44px;z-index:500;display:-ms-flexbox;display:flex;-ms-flex-align:center;align-items:center;color:#FFF;box-sizing:border-box
}
.job-header.primary{background-color:#1ac788;border-bottom:1px solid rgba(255,255,255,0.1)
}
.job-header__search{display:-ms-flexbox;display:flex;-ms-flex-align:center;align-items:center;-ms-flex-pack:center;justify-content:center;width:100%;height:30px;border:1px solid rgba(255,255,255,0.8);border-radius:5px
}
.job-header__search .icon{font-size:18px
}
.job-header__search .txt{margin-left:10px;font-size:12px
}
.job-header ~ .job-content{top:44px;overflow-y:auto
}
.job-banner{text-align:center
}
.job-content{width:100%;position:absolute;bottom:50px
}



