
.job-info-main{position:relative;padding:10px 15px;height:90px;background:#FFF
}
.job-info-sub{display:-ms-flexbox;display:flex;-ms-flex-pack:justify;justify-content:space-between;-ms-flex-align:center;align-items:center;padding:0 15px;font-size:12px;height:28px;background:#f4f4f4
}
.job-info-text{display:-ms-flexbox;display:flex;-ms-flex-pack:justify;justify-content:space-between;-ms-flex-align:center;align-items:center;margin:14px 0 14px 80px;line-height:1
}
.job-info__thumb{position:absolute;width:70px;height:70px;line-height:70px;text-align:center;border-radius:50%;background:#e2e2e2;color:#FFF;font-weight:bold
}
.job-info__name{-ms-flex:1;flex:1;overflow:hidden
}
.job-info__name .job{max-width:100%;font-size:15px;font-weight:600;white-space:nowrap;overflow:hidden;text-overflow:ellipsis;display:inline-block;padding-right:20px;vertical-align:middle
}
.job-info__name .type{display:inline-block;margin-left:-20px;vertical-align:middle;font-size:10px;width:15px;height:15px;line-height:15px;text-align:center;border-radius:3px
}
.job-info__name .type.type-hot{background:#e14426;color:#FFF
}
.job-info__name .type.type-urgent{background:#0fcfb3;color:#FFF
}
.job-info__date{width:65px;text-align:right;color:#999;font-size:12px
}
.job-info__addr{font-size:14px;color:#999
}
.job-info__addr .icon{margin-right:5px
}
.job-info__salary{color:#e31915;font-size:18px;font-weight:600
}
.job-info__desc{color:#999
}
.job-info__desc .tips{display:inline-block;padding:2px 5px;margin-right:5px;line-height:1;color:#12b699;border:1px solid #12b699;font-size:10px;border-radius:3px
}
.job-info__company{color:#666
}



