
.my-info{padding:20px;position:relative;background:#fff
}
.my-info-main{display:inline-block
}
.my-info-head,.my-info-desc{display:inline-block
}
.my-info-head{margin-right:15px
}
.my-info-head img{width:60px;height:60px
}
.my-info-update{position:absolute;top:50%;right:30px;transform:translate(0, -50%)
}
.my-info .update-button{height:40px;line-height:40px
}
.my-info-desc h1{font-weight:normal
}
.my-info-nodata{height:80px;line-height:80px;text-align:center;background:#fff
}
.my-info-nodata .weui-icon-warn{margin-right:10px
}



